import OrderList from '../../components/orderList/orderList.vue'
export default{
	components:{
		OrderList
	},
	data(){
		return{
			top:[
				{name:'全部',status:100},
				{name:'已付款',status:1},
				{name:'已完成',status:8},
				{name:'已失效',status:4}
				],
				tabIndex:0,
				Maindata:[
				],
				tomName:'全部',
				topStatus:100
		}
	},
	methods:{
		orderIndex(i,n){
			this.tabIndex=n;
			this.topStatus=i.status;
		}
	},
	onLoad:function(e){
		console.log("页面传值:",e);
		uni.setNavigationBarTitle({
			title:e.name
		})
		// 商品请求
		uni.request({
			url: e.quest,
			method: 'GET',
			data: {
				openid:e.get
			},
			success: res => {
				console.log("order数据状态:",res);
				this.Maindata=res.data.data.my_order;
			},
			fail: () => {},
			complete: () => {}
		});
	}
}
